.card {
  box-sizing: border-box;
  width: var(--card-width);
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  background: var(--color-white);
}

.card__image {
  display: block;
  margin: 0 auto 40px;
  width: 160px;
  height: 134px;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  line-height: 18px;
  font-weight: 500;
  color: var(--text-accent-color);
}

.card__code {
  margin-bottom: 16px;
}

.card__review-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
}

.card__review-count {
  color: var(--text-accent-color);
}

.card__price-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.card__price-label {
  font-size: 12px;
  line-height: 18px;
  margin: 0;
}

.card__price {
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
  color: var(--text-accent-color);
  margin: 0;
}

.card__buy {
  display: flex;
  justify-content: center;
  align-items: center;
  
  box-sizing: border-box;
  height: 40px;
  border: 1px solid var(--main-color);
  border-radius: 5px;
  
  font-size: 14px;
  line-height: 16px;
  color: var(--color-white);
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  
  background-color: var(--main-color);
}

.card__buy:hover {
  color: var(--main-color);
  background-color: var(--color-white);
}
